import { By } from 'selenium-webdriver';
import { DriverLike, register } from '@oracle/oraclejet-webdriver';
import { TagCloudWebElement } from './TagCloudWebElement';
export { TagCloudWebElement };

/**
 * Retrieve an instance of [[TagCloudWebElement]].
 * @example
 * ```javascript
 * import { findTagCloud } from '@oracle/oraclejet-core-pack/webdriver';
 * const el = await findTagCloud(driver, By.id('my-oj-c-tag-cloud'));
 * ```
 * @param driver A WebDriver/WebElement instance from where the element will be
 * searched. If WebDriver is passed, the element will be searched globally in the
 * document. If WebElement is passed, the search will be relative to this element.
 * @param by The locator with which to find the element
 */
export async function findTagCloud(driver: DriverLike, by: By): Promise<TagCloudWebElement> {
  const webEl = await driver.findElement(by);
  // Check that the element is of type TagCloudWebElement
  if (!(webEl instanceof TagCloudWebElement)) {
    const tagName = await webEl.getTagName();
    throw Error(
      `findTagCloud(${by}) created ${webEl.constructor.name} for <${tagName}>, but expected ${TagCloudWebElement.name}`
    );
  }
  return webEl;
}

register('oj-c-tag-cloud', TagCloudWebElement);
